<template>
  <header class="header">
    <div class="navbar">
      <div class="navbar-left-container">
        <span>
          <router-link to="/">
          <img class="navbar-brand-logo" src="/static/logo-new.svg">
          </router-link>
        </span>
      </div>
      <div class="navbar-right-container">
        <div class="navbar-menu-container">
          <ul>
            <li>当前城市:</li>
            <li class="dropdown-menu">
              <span class="current">{{current}}</span>
              <span class="arrow"></span>
              <ul class="dropdown-content">
                <li v-for="(item,key) in cityList">
                  <a href="javascript:void(0);" @click="changeCity(key)">{{item}}</a>
                </li>
              </ul>
            </li>
            <li class="nav-statis">
              <router-link to="/info">统计信息</router-link>
            </li>
            <li class="nav-about" @click="mdShow=true">关于</li>
          </ul>
        </div>
      </div>
    </div>
    <modal v-bind:mdShow="mdShow" @close="closeModal">
      <p slot="message">
        本站所有信息均来自其他网站,不对信息的正确性负责.
      </p>
      <div slot="btnGroup">
        <a class="btn btn--m" href="javascript:;" @click="mdShow = false">关闭</a>
      </div>
    </modal>

  </header>
</template>

<script>
  import './../assets/css/base.css'
  import './../assets/css/product.css'
  import Modal from '@/components/Modal'

  export default {
    name: "nav-header",

    data() {
      return {
        cityList: {'sz': '深圳', 'gz': '广州', 'sh': '上海', 'nj': '南京', 'hz': '杭州'},
        current: '深圳',
        mdShow: false,   // 是否显示关于页面
      }
    },
    components: {
      Modal,
    },
    methods: {
      changeCity(city) {   // 改变当前城市
        console.log(city);
        this.current = this.cityList[city];
        this.$emit('change', city);
      },
      closeModal() {
        this.mdShow = false;
      }
    }
  }
</script>
<style>
  .navbar-menu-container ul li {
    float: left;
  }

  .nav-about, .nav-statis {
    padding-left: 25px;
  }

  @media only screen and (max-width: 767px) {
    .nav-about, .nav-statis {
      padding-left: 10px;
    }
  }

  .navbar-menu-container .nav-about:hover {
    color: #42b983;
    cursor: pointer;
  }

  .dropdown-menu {
    position: relative;
    margin: 0 10px;
    display: inline-block;
  }

  .dropdown-menu .current {
    border-bottom: 3px solid #42b983;
  }

  .dropdown-menu .arrow {
    pointer-events: none;
    display: inline-block;
    vertical-align: middle;
    margin-top: -1px;
    margin-left: 6px;
    margin-right: -14px;
    width: 0;
    height: 0;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 5px solid #ccc;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    top: 100%;
    right: -20px;
    min-width: 140px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    padding: 16px 16px;
  }

  .dropdown-content li {
    padding: 10px;
  }

  .dropdown-content li:hover a {
    color: #42b983;
  }

  .dropdown-menu:hover .dropdown-content {
    display: block;
    background-color: #fff;
    z-index: 999;
  }

</style>




