import Vue from 'vue'
import Router from 'vue-router'
import RoomsList from '@/views/RoomsList'
import Index from '@/views/Index'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'RoomsList',
      component: RoomsList
    },
    {
      path: '/info',
      name: 'Index',
      component: Index
    },

  ]
})



