<template>
  <div>
    <nav-header @change="cityChange"></nav-header>
    <nav-bread>
      <span>info</span>
    </nav-bread>
    <div class="accessory-result-page accessory-page charts-page">
      <div class="container">
        <div class="accessory-result">
          <!-- filter -->
          <div class="filter stopPop" id="filter" :class="{'filterby-show':filterBy}">
            <dl class="filter-price">
              <dt>Charts</dt>
              <dd><a href="javascript:void(0)" @click="goAnchor('#typecounts')">数量对比</a></dd>
              <dd><a href="javascript:void(0)" @click="goAnchor('#numscompare')">租房方式价格对比</a></dd>
              <dd><a href="javascript:void(0)" @click="goAnchor('#counts')">区域房源统计</a></dd>
              <dd><a href="javascript:void(0)" @click="goAnchor('#areanums')">区域房源居室统计</a></dd>
              <dd><a href="javascript:void(0)" @click="goAnchor('#average')">租房平均价格</a></dd>
            </dl>
          </div>
          <div class="accessory-list-wrap">
            <div class="accessory-list col-4">
              <chart :id="'typecounts'" :option="typeCounts" class="chart"></chart>
              <chart :id="'numscompare'" :option="roomnumscompare" class="chart"></chart>
              <chart :id="'counts'" :option="counts" class="chart"></chart>
              <chart :id="'areanums'" :option="areanums" class="chart"></chart>
              <chart :id="'average'" :option="average" class="chart"></chart>
            </div>
          </div>
        </div>

      </div>
    </div>
    <modal v-bind:mdShow="mdShowTips" @close="closeModal">
      <p slot="message">
        {{mdShowMessage}}
      </p>
      <div slot="btnGroup">
        <a class="btn btn--m" href="javascript:;" @click="mdShowTips = false">关闭</a>
      </div>
    </modal>
    <nav-footer></nav-footer>
  </div>
</template>

<script>
  import './../assets/css/base.css'
  import './../assets/css/product.css'
  import axios from 'axios'
  import NavHeader from '@/components/NavHeader'
  import NavBread from '@/components/NavBread'
  import NavFooter from '@/components/NavFooter'
  import Modal from '@/components/Modal'
  import Chart from '@/components/Chart'

  export default {
    name: "index",
    data() {
      return {
        msg: 'Hello world!',
        // base_url: 'http://192.168.137.1:5001/api/info/',
        base_url: 'http://118.24.147.149:5000/api/info/',
        area_data: [],
        city_name: '',
        rent_type: ['整租', '合租'],
        nums_type: ['一室', '二室', '三室', '四室及以上'],
        mdShowTips: false,   // 展示获取数据提示
        filterBy: false,
        overLayFlag: false,
        mdShowMessage: '',
        typeCounts: {
          tooltip: {},
          title: [{
            text: '整租和合租数量对比',
            subtext: '',
            x: '25%',
            textAlign: 'center'
          }, {
            text: '房间数量对比',
            subtext: '',
            x: '70%',
            textAlign: 'center'
          }],
          legend: {
            x: 'center',
            y: 'bottom',
          },
          series: [{
            type: 'pie',
            radius: [0, '50%'],
            center: ['25%', '50%'],
            data: []
          }, {
            type: 'pie',
            radius: [30, 110],
            roseType: 'area',
            center: ['75%', '50%'],
            data: []
          }]
        },
        roomnumscompare: {
          title: {
            text: '',
            subtext: '',
            show: true,
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: []
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: []
          },
          yAxis: {
            type: 'value',
            axisLabel: {
              formatter: '{value} 元'
            }
          },
          series: []
        },
        counts: {    // 房源数量统计
          title: {
            text: '',
            subtext: '',
            show: '',
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: []
          },
          calculable: true,
          xAxis: [
            {
              type: 'category',
              data: []
            }
          ],
          yAxis: [
            {
              type: 'value',
              name: '房源数',
            }
          ],
          series: []
        },
        areanums: {
          title: {
            text: '',
            subtext: '',
            show: true,
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {            // 坐标轴指示器，坐标轴触发有效
              type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          legend: {
            data: []
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'value'
          },
          yAxis: {
            type: 'category',
            data: []
          },
          series: []
        },
        average: {
          title: {
            text: '',
            subtext: '',
            show: true,
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: []
          },
          calculable: true,
          xAxis: [
            {
              type: 'category',
              data: [],
              axisPointer: {
                type: 'shadow'
              }
            }
          ],
          yAxis: [
            {
              type: 'value',
              name: '平均租金/元',
              // max: 15000,
            }, {
              type: 'value',
              name: '最高租金/元',
              // max: 100000
            }
          ],
          series: []
        },
      }
    },
    components: {
      NavHeader,
      NavBread,
      NavFooter,
      Modal,
      Chart,
    },
    mounted: function () {
      this.getCityInfo('sz');
      this.handlReSize(null);
      window.addEventListener('resize', this.handlReSize);
    },
    methods: {
      getCityInfo(city) {
        const url = this.base_url + city;
        this.mdShowTips = true;
        this.mdShowMessage = '正在获取数据中,请稍后... ...';
        axios.get(url).then((result) => {
          let res = result.data;
          if (res.status == 0) {
            // 获取数据成功
            this.city_name = res.city;
            let info_datas = res.result;
            this.inittypeCountsChart(info_datas);
            this.initroomnumscompareChart(info_datas);
            this.initCountChart(info_datas);
            this.initAreaNumsChart(info_datas);
            this.initAverageChart(info_datas);
            this.mdShowTips = false;
          } else {
            this.mdShowMessage = res.msg;
          }
        }).catch((error) => {
          this.mdShowMessage = '服务器请求错误,请稍后再重试...';
        })
      },
      inittypeCountsChart(info_datas) {
        let total_series_data = [];
        let roomnum_series_data = [];
        let temp_roomnum_data = {};
        for (var key of this.rent_type) {
          let total_series_item = {'name': key};
          let nums_key = key + 'a';
          let total_item_value = 0;
          let roomnum_info = info_datas[nums_key];
          for (var i = 0; i < roomnum_info.length; i++) {
            var this_counts = roomnum_info[i]['counts'];
            total_item_value += roomnum_info[i]['counts'];
            var area_id = roomnum_info[i]['_id'];
            if (area_id in temp_roomnum_data) {
              temp_roomnum_data[area_id] += roomnum_info[i]['counts'];
            } else {
              temp_roomnum_data[area_id] = roomnum_info[i]['counts'];
            }
          }
          total_series_item['value'] = total_item_value;
          total_series_data.push(total_series_item);
        }
        for (let nums in temp_roomnum_data) {
          let temp_object = {'name': nums, 'value': temp_roomnum_data[nums]};
          roomnum_series_data.push(temp_object);
        }
        this.typeCounts.series[0].data = total_series_data;
        this.typeCounts.series[1].data = roomnum_series_data;
      },
      initroomnumscompareChart(info_datas) {
        let xAxis_data = this.roomnumscompare.xAxis.data;
        xAxis_data.length = 0;
        let series = this.roomnumscompare.series;
        series.length = 0;
        for (var key of this.rent_type) {
          let nums_key = key + 'a';
          let series_item = {
            name: key,
            type: 'line',
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
            markLine: {
              data: [
                {type: 'average', name: '平均值'}
              ]
            },
            data: []
          };
          let type_infos = info_datas[nums_key];
          for (var i = 0; i < type_infos.length; i++) {
            let info = type_infos[i];
            if (xAxis_data.indexOf(info['_id']) == -1) {
              xAxis_data.push(info['_id']);
            }

            let index = xAxis_data.indexOf(info['_id']);
            series_item['data'][index] = Math.round(info['average']);
          }
          series.push(series_item);
        }
        this.roomnumscompare.title.text = '不同租房方式平均价格对比';
        this.roomnumscompare.legend.data = this.rent_type;
      },
      initCountChart(info_datas) {   // 更新房源统计视图
        let xAxis_data = [];
        let series = [];
        for (var key of this.rent_type) {
          let series_item = {
            label: {
              normal: {
                show: true,
                position: 'top'
              }
            }
          };
          series_item['name'] = key;
          series_item['type'] = 'bar';
          series_item['data'] = [];
          let type_infos = info_datas[key];
          for (var i = 0; i < type_infos.length; i++) {
            let info = type_infos[i];
            if (xAxis_data.indexOf(info['_id']) == -1) {
              xAxis_data.push(info['_id']);
            }

            let index = xAxis_data.indexOf(info['_id']);
            series_item['data'][index] = info['counts'];
          }
          series.push(series_item);
        }
        this.counts.title.text = this.city_name + "房源数量统计";
        this.counts.legend.data = this.rent_type;
        this.counts.xAxis[0].data = xAxis_data;
        this.counts.series = series;
      },
      initAreaNumsChart(info_datas) {
        this.areanums.yAxis.data = [];
        let yAxis_data = this.areanums.yAxis.data;
        let series = this.areanums.series;
        series.length = 0;
        for (let key of this.nums_type) {
          let series_item = {
            name: key,
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight'
              }
            },
            data: []
          };
          let type_infos = info_datas[key];
          for (var i = 0; i < type_infos.length; i++) {
            let info = type_infos[i];
            if (yAxis_data.indexOf(info['_id']) == -1) {
              yAxis_data.push(info['_id']);
            }
            let index = yAxis_data.indexOf(info['_id']);
            series_item['data'][index] = info['counts'];
          }
          series.push(series_item);
        }
        this.areanums.title.text = this.city_name + "各居室出租数量统计";
        this.areanums.legend.data = this.nums_type;
      },
      initAverageChart(info_datas) {
        let xAxis_data = [];
        let series = [];
        let legend_data = [];
        let fullrent_max_price = {
          name: '整租最高价',
          type: 'line',
          yAxisIndex: 1,
          data: []
        };
        let sharerent_max_price = {
          name: '合租最高价',
          type: 'line',
          yAxisIndex: 1,
          data: []
        };
        legend_data.push(...this.rent_type, '整租最高价', '合租最高价');
        for (var key of this.rent_type) {
          let now_temp_type = [];
          if (key == '整租') {
            now_temp_type = fullrent_max_price.data;
          } else {
            now_temp_type = sharerent_max_price.data;
          }
          let series_item = {
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            }
          };
          series_item['name'] = key;
          series_item['type'] = 'bar';
          series_item['data'] = [];
          let type_infos = info_datas[key];
          for (var i = 0; i < type_infos.length; i++) {
            let info = type_infos[i];
            if (xAxis_data.indexOf(info['_id']) == -1) {
              xAxis_data.push(info['_id']);
            }

            let index = xAxis_data.indexOf(info['_id']);
            series_item['data'][index] = Math.round(info['average']);
            now_temp_type[index] = parseInt(info['max']);
          }
          series.push(series_item);
        }
        series.push(fullrent_max_price, sharerent_max_price);
        this.average.title.text = this.city_name + "平均租房价格";
        this.average.legend.data = legend_data;
        this.average.xAxis[0].data = xAxis_data;
        this.average.series = series;
      },
      cityChange(city) {
        // console.log(city);
        this.getCityInfo(city);
      },
      goAnchor(selector) {
        var anchor = this.$el.querySelector(selector);
        window.scrollTo({"behavior": "smooth", "top": anchor.offsetTop});
      },
      handlReSize(event) {
        let clientWidth = document.body.clientWidth;
        let isBig = true;
        if (clientWidth < 768) {
          isBig = false;
          this.typeCounts.series[0].radius = [0, '30%'];
          this.typeCounts.series[1].radius = [20, 80];
        } else {
          this.typeCounts.series[0].radius = [0, '50%'];
          this.typeCounts.series[1].radius = [30, 110];
        }
        this.areanums.title.show = isBig;
        this.counts.title.show = isBig;
        this.roomnumscompare.title.show = isBig;
        this.average.title.show = isBig;
      },
      closeModal() {
        this.mdShowTips = false;
      },
      showFilterPop() {
        this.filterBy = true;
        this.overLayFlag = true;
      },
      closePop() {
        this.filterBy = false;
        this.overLayFlag = false;
      },
    },
  }
</script>

<style>
  .charts-page {
    margin: 40px 0;
  }

  .accessory-list, .accessory-list-wrap, .accessory-result {
    width: 100%;
  }

  .chart {
    margin-bottom: 50px;
  }

  @media only screen and (max-width: 767px) {
    .chart {
      margin-bottom: 30px;
    }
  }
</style>



